// var util = require('../../../common/util.js')
export default {
	data() {
		return {
			newPwd: '',
			reNewPwd:'',
			userName:'',
			mobile:''
		}
	},
	onLoad(e) {
		if(e['userName']) {
			console.log(e);
			var userName = e['userName'];
			var mobile = e['mobile'];
			var pwd = e['pwd'];
			this.userName = userName;
			this.mobile = mobile;
			this.pwd = pwd;
		}
	},
	methods: {
		modifyCode(data){
			uni.request({
				url:this.websiteUrl + '/user_pwd_reset',
				method:'POST',
				data:data,
				dataType:'json',//服务器返回json格式数据
				header:{'Content-Type':'application/json','Token':''},
				success:res=>{
					var res = res.data;
					//服务器返回响应，根据响应结果，分析是否登录成功；
					var that = this;
					if(res.code == 200){
						console.log("修改密码成功："+JSON.stringify(res))
						uni.showToast({
							icon: 'success',
							title: '修改密码成功',
							duration:1000
						});
						uni.navigateTo({
							url:'/pages/login/login/login'
						})
					}else {
						uni.showToast({
							icon: 'none',
							title: res.message,
						});
					}
				},
				fail: () => {},
				complete: () => {}
			})
		},
		toSetPassword(){
			console.log("点击下一步，修改密码");
			let that = this;
			let userName = this.userName;
			let pwd = this.pwd;
			let reNewPwd = this.reNewPwd;
			let newPwd = this.newPwd;
			let data = {
				'userName': userName,
				'pwd': pwd,
				'newPwd': newPwd,
				'reNewPwd': newPwd,
				'type': 1
			}
			if(!newPwd) {
				uni.showToast({
					icon: 'none',
					title: '请输入密码',
				});
				return;
			}else if(reNewPwd != newPwd){
				uni.showToast({
					icon: 'none',
					title: '两次密码不正确',
				});
			}else {
				this.modifyCode(data);
			}
			
		}
	}
}
